import { EventEmitter, Input, Output, Directive, TemplateRef } from '@angular/core';

import { convertToBoolean, convertToInt } from '../../utils/util';

import { PoProgressStatus } from './enums/po-progress-status.enum';
import { PoProgressSize } from './enums/po-progress-size.enum';

const poProgressMaxValue = 100;
const poProgressMinValue = 0;

/**
 * @description
 *
 * Componente de barra de progresso que possibilita exibir visualmente o progresso/carregamento de uma tarefa.
 *
 * Este componente pode ser utilizado no *upload* de arquivos, uma atualizao no sistema ou o processamento de uma imagem.
 *
 * #### Tokens customizveis
 *
 *  possvel alterar o estilo do componente usando os seguintes tokens (CSS):
 *
 * > Para maiores informaes, acesse o guia [Personalizando o Tema Padro com Tokens CSS](https://po-ui.io/guides/theme-customization).
 *
 * | Propriedade                            | Descrio                                             | Valor Padro                                    |
 * |----------------------------------------|-------------------------------------------------------|-------------------------------------------------|
 * | **Default Values**                     |                                                       |                                                 |
 * | `--font-family`                        | Famlia tipogrfica usada                             | `var(--font-family-theme)`                      |
 * | `--text-color`                         | Cor do texto                                          | `var(--color-neutral-dark-90)`                  |
 * | **Error**                              |                                                       |                                                 |
 * | `--text-color-error`                   | Cor do texto no estado error                          | `var(--color-feedback-negative-dark)`           |
 * | `--color-icon-error`                   | Cor do cone no estado error                          | `var(--color-feedback-negative-dark)`           |
 * | **po-progress-bar**                    |                                                       |                                                 |
 * | `--background-color-tray`              | Cor do background                                     | `var(--color-brand-01-lightest)`                |
 * | `--background-color-indicator`         | Cor do background do indicador                        | `var(--color-action-default)`                   |
 *
 */
@Directive()
export class PoProgressBaseComponent {
  /**
   * @optional
   *
   * @description
   *
   * Informao adicional que aparecer abaixo da barra de progresso ao lado direito.
   */
  @Input('p-info') info?: string;

  /**
   * @optional
   *
   * @description
   *
   * cone que aparecer ao lado do texto da propriedade `p-info`.
   *
   * Exemplo: `po-icon-ok`.
   */
  @Input('p-info-icon') infoIcon?: string | TemplateRef<void>;

  /**
   * @optional
   *
   * @description
   *
   * Status da barra de progresso que indicar visualmente ao usurio
   * o andamento, por exemplo, se a mesma foi concluda com sucesso.
   *
   * @default `PoProgressStatus.Default`
   */
  @Input('p-status') status: PoProgressStatus = PoProgressStatus.Default;

  /**
   * @optional
   *
   * @description
   *
   * Texto principal que aparecer abaixo da barra de progresso no lado esquerdo.
   */
  @Input('p-text') text?: string;

  /**
   * @optional
   *
   * @description
   *
   * Evento que ser disparado ao clicar no cone de cancelamento ("x") na parte inferior da barra de progresso.
   *
   * Ao ser disparado, a funo receber como parmetro o status atual da barra de progresso.
   *
   * > Se nenhuma funo for passada para o evento ou a barra de progresso estiver com o status `PoProgressStatus.Success`,
   * o cone de cancelamento no ser exibido.
   */
  @Output('p-cancel') cancel: EventEmitter<any> = new EventEmitter();

  /**
   * @optional
   *
   * @description
   *
   * Evento que ser disparado ao clicar no cone de tentar novamente na parte inferior da barra de progresso.
   *
   * > o cone ser exibido apenas se informar uma funo neste evento e o status da barra de progresso for
   * `PoProgressStatus.Error`.
   */
  @Output('p-retry') retry: EventEmitter<any> = new EventEmitter();

  private _indeterminate?: boolean;
  private _value?: number = 0;
  private _size: string = 'large';

  /**
   * @optional
   *
   * @description
   *
   * Habilita o modo indeterminado na barra de progresso, que mostra uma animao fixa sem um valor estabelecido.
   *
   * Esta opo pode ser utilizada quando no souber quanto tempo levar para que um processo seja concludo.
   *
   * > Caso esta propriedade e a `p-value` seja habilitada, a propriedade `p-value` ser ignorada.
   *
   * @default `false`
   */
  @Input('p-indeterminate') set indeterminate(indeterminate: boolean) {
    this._indeterminate = convertToBoolean(indeterminate);
  }

  get indeterminate(): boolean {
    return this._indeterminate;
  }

  /**
   * @optional
   *
   * @description
   *
   * Valor que representar o progresso.
   *
   * > Os valores aceitos so nmeros inteiros de `0`  `100`.
   *
   * @default `0`
   */
  @Input('p-value') set value(value: number) {
    const integerValue = convertToInt(value, poProgressMinValue);
    const isProgressRangeValue = this.isProgressRangeValue(integerValue);

    this._value = isProgressRangeValue ? integerValue : poProgressMinValue;
  }

  get value() {
    return this._value;
  }

  /**
   * @optional
   *
   * @description
   *
   * Definio do tamanho da altura da barra de progresso.
   *
   * Valores vlidos:
   *  - `medium`: tamanho mdio
   *  - `large`: tamanho grande
   *
   * @default `large`
   */
  @Input('p-size') set size(value: string) {
    this._size = PoProgressSize[value] ? PoProgressSize[value] : PoProgressSize.large;
  }

  get size(): string {
    return this._size;
  }

  /**
   * @optional
   *
   * @description
   *
   * Ativa a exibio da porcentagem atual da barra de progresso.
   *
   * @default `false`
   */
  @Input({ alias: 'p-show-percentage', transform: convertToBoolean }) showPercentage: boolean = false;

  private isProgressRangeValue(value: number): boolean {
    return value >= poProgressMinValue && value <= poProgressMaxValue;
  }
}
